<template>
  <div class="content"  @click="contenClick">
    <!--     下拉框-->
    <Select2></Select2>
    <!--   统计表-->
    <div class="statis  statisTop">
      <div class="statiStical">
        <h2><span></span><p>收益数据<img @click="labelClick" src="../../../assets/wh.png" alt=""></p>
          <div v-if="labelShow" class="labelText">指使用中订单、已完成租车订单的租赁费用</div></h2>
        <ul>
          <li>
            <div>
              <span>收益总计</span>
              <p>{{EarningsData.totalAmount}}</p>
            </div>
          </li>
          <li>
            <span>单日最高收益</span>
            <p>{{EarningsData.maxAmount}}</p>
          </li>
          <li>
            <span>日均收益</span>
            <p>{{EarningsData.avgAmount}}</p>
          </li>
        </ul>
      </div>
      <div class="marginTop">
        <div id="eCharId1" class="eChars"></div>
        <div class="marginTop">
          <timeList  @timeChang="timeChang"></timeList>
        </div>
      </div>
    </div>
<!--    <div class="statis WithdrawalData">-->
<!--      <div class="statiStical">-->
<!--        <h2><span></span><p>提现数据</p></h2>-->
<!--        <ul>-->
<!--          <li>-->
<!--            <div>-->
<!--              <span>提现总计</span>-->
<!--              <p>{{AmountData.cashAmount}}</p>-->
<!--            </div>-->
<!--          </li>-->
<!--          <li>-->
<!--            <span>单日最高提现</span>-->
<!--            <p>{{AmountData.theHighestCash}}</p>-->
<!--          </li>-->
<!--          <li>-->
<!--            <span>日均提现</span>-->
<!--            <p>{{AmountData.averageDaily}}</p>-->
<!--          </li>-->
<!--        </ul>-->
<!--      </div>-->
<!--      <div class="marginTop">-->
<!--        <div id="eCharId2" class="eChars"></div>-->
<!--        <div class="marginTop">-->
<!--          <timeList  @timeChang="timeChang2"></timeList>-->
<!--        </div>-->
<!--      </div>-->
<!--    </div>-->
  </div>
</template>
<script>
  const timeList = () => import("../../../components/timeList2.vue");
  const Select2 = () => import("../../../components/Select2.vue");
  export default {
    components:{
      timeList,
      Select2
    },
    data() {
      return {
        labelShow:false,
        day:this.common.timeForMat(7,true)+" - "+this.common.timeForMat(0,true),
        itemId:"-2",
        pathData:{
          beginDate:this.common.timeForMat(7,false),
          endDate:this.common.timeForMat(0,false),
          subAgentId:this.$store.state.subAgentId
        },
        pathData2:{
          minTime:this.common.timeForMat(7),
          maxTime:this.common.timeForMat(0),
        },
        AmountData:{
          averageDaily:"",
          cashAmount:"",
          theHighestCash:"",
        },
        EarningsData:{
          avgAmount:0,
          maxAmount:0,
          totalAmount:0,
        }
      }
    },
    computed:{
      changSelrct() {
        return this.$store.state.subAgentId
      }
    },
    watch:{
      changSelrct(val){
        this.pathData.subAgentId=val;
        this.storeAmount();
      }
    },
    mounted(){
      if(this.$store.state.subAgentId!=""){
        this.storeAmount();
      }
    },
    methods:{
      labelClick(e){
        e.stopPropagation();
        if(this.labelShow){
          this.labelShow=false;
        }else {
          this.labelShow=true;

        }
      },
      contenClick(){
        this.labelShow=false;
      },
      storeAmount(){
        var that=this;
        this.http.get3("csh_m/statCenter/subAgentAmount/"+this.$store.state.pathCode,this.pathData,this.$store.state.token)
          .then(res => {
            if(res.data.code==600){
              if(res.data.result==null){
                this.common.eChart("eCharId1",[],[]);
                that.EarningsData.avgAmount=0;
                that.EarningsData.maxAmount=0;
                that.EarningsData.totalAmount=0;
              }else {
                let dataList=res.data.result.dataList;
                that.EarningsData.avgAmount=that.common.Money2(res.data.result.avgAmount);
                that.EarningsData.maxAmount=that.common.Money2(res.data.result.maxAmount);
                that.EarningsData.totalAmount=that.common.Money2(res.data.result.totalAmount);
                let amountArr=[];
                let timesArr=[];
                dataList.forEach(function(item,index) {
                  amountArr.push(that.common.Money2(item.orderAmount));
                  timesArr.push(item.statDate);
                })
                this.common.eChart("eCharId1",amountArr,timesArr);
              }
            }else{
              this.$dialog.alert({
                message:"获取收益数据失败！"
              }).then(() => {
              });
            }
          })
      },
      overviewData(){//提现数据
        var that=this;
        this.http.get2("cashrecord/overviewData",this.pathData2,this.$store.state.token)
          .then(res => {
            console.log(res)
            if(res.data.resultCode==1){
              let amountByDayVOS=res.data.data.amountByDayVOS;
              that.AmountData.averageDaily=that.common.Money2(res.data.data.averageDaily);
              that.AmountData.cashAmount=that.common.Money2(res.data.data.cashAmount);
              that.AmountData.theHighestCash=that.common.Money2(res.data.data.theHighestCash);

              let amountArr=[];
              let timesArr=[];
              amountByDayVOS.forEach(function(item,index) {
                amountArr.push(that.common.Money2(item.amount));
                timesArr.push(item.times);
              })
              this.common.eChart("eCharId2",amountArr,timesArr);
            }else{
              this.selectList=[];
              this.$dialog.alert({
                message:"获取提现数据失败！"
              }).then(() => {
              });
            }
          })
      },
      timeChang(data){
        if(data.length>10){
          this.pathData.beginDate=data.split(" - ")[0];
          this.pathData.endDate=data.split(" - ")[1];
        }else {
          this.pathData.beginDate=data;
          this.pathData.endDate=this.common.timeForMat(0,false);
        }
        this.storeAmount();
      },
      timeChang2(data){
        if(data.length>10){
          this.day=this.common.transForm(data);
          this.pathData2={
            minTime:this.common.stateLong(data.split(" - ")[0]),
            maxTime:this.common.stateLong(data.split(" - ")[1])
          }
        }else {
          this.pathData2={
            minTime:this.common.stateLong(data),
            maxTime:this.common.timeForMat(0)
          };
        }
        this.overviewData();
      },
    },
  }
</script>
<style scoped lang="scss">
  .eChars{
    width: 100%;
    height: 472px;
  }
  .marginTop{
    margin-top:20px;
  }
  .statis{
    background: #fff;
    padding-bottom: 42px;

  }
  .statisTop{
    margin-top:100px;
  }
  .WithdrawalData{
    border-top: 20px solid #F8F8F8;
    padding-bottom: 150px;
  }
  .statiStical{
    padding-top: 20px;
  }
  .statiStical h2{
    height: 60px;
    padding: 10px  36px;
    position: relative;
    .labelText{
      width:592px;
      height:80px;
      background:rgba(102,102,102,1);
      border-radius:8px;
      font-size:28px;
      font-family:PingFangSC;
      font-weight:400;
      color:rgba(255,255,255,1);
      line-height:80px ;
      position: absolute;
      left: 60px;
      top: 70px;
    }

  }
  .statiStical  h2 p{
    text-align: left;
    float: left;
    height: 40px;
    line-height: 40px;
    font-size:28px;
    font-family:PingFangSC;
    font-weight:500;
    color:rgba(51,51,51,1);
    margin-left: 20px;
    position: relative;
    img{
      height:28px;
      width:28px;
      background-size:100% 100%;
      position: absolute;
      right: -38px;
      top: 6px;
    }
  }
  .statiStical  h2 span{
    height: 28px;
    width: 10px;
    background: #FF8D59;
    float: left;
    margin-top: 6px;
  }
  .statiStical ul{
    margin-top: 20px;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
  }
  .statiStical ul li{
    flex: 1;
    padding-left: 30px;
    box-sizing: border-box;
    border-left: 1px solid #E6E8EE;
  }
  .statiStical ul li:nth-child(1){
    border: none;
  }
  .statiStical ul li div{
    margin: 0 auto;
    display: inline-block;
  }
  .statiStical ul li span{
    font-size:24px;
    font-family:PingFangSC;
    font-weight:400;
    color:rgba(153,153,153,1);
    line-height:34px;
    text-align: left;
    display: block;
  }
  .statiStical ul li p{
    text-align: left;
    font-size:48px;
    font-family:PingFangSC;
    font-weight:600;
    color:rgba(51,51,51,1);
    line-height:76px;
  }
  .content{
    min-height: 100%;
  }

</style>



